      </div>
    </div>
</template>

<script>
    export default {
      name: "finance",
      methods:{
        linkHome(){
          this.$router.push({
            name:'home'
          })
        },
        getText(){
          let iframe = document.getElementById('ifr').getElementsByClassName('');
          console.log(iframe)
        },
        getIframeDocument(iframe) {
          return iframe.contentDocument || iframe.contentWindow.document;
        }
      }
    }
</script>
<style scoped lang="scss">
  @import "../../style/my";
  html,body,#show-iframe{
    padding:0;
    margin:0;
    width:100%;
    height:100%
  }
  #show-iframe{
    border:0;
  }
</style>



